import React, { useState, useCallback, useEffect } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import styles from '../pages/css/community.module.css';
import { GET, POST } from '../utils/requests';
import { ProfileData, Team } from '../interfaces';
import { FaCrown, FaCircle, FaPlusCircle } from 'react-icons/fa';
import AutoComplete from '../components/AutoComplete';

export default function Teams(props: RouteComponentProps<any, StaticContext, any> & {
	direction: string
	profile: ProfileData
	updateProfile: () => void
}) {

	return (
		<section className={['container'].join(' ')}>
			This user has no teams
		</section>
	)
}

export function Members(props: RouteComponentProps<any, StaticContext, any> & {
	team: Team & ProfileData
	updateProfile: () => void
	isAuthed: boolean
}) {

	const [users, setUsers] = useState([]);
	const updateUsers = useCallback(() => GET({ url: '/users' })
		.then(setUsers), [setUsers]);
	useEffect(() => {
		updateUsers();
	}, [updateUsers]);

	const [admins, setAdmins] = useState([] as ProfileData[]);
	const [members, setMembers] = useState([] as ProfileData[]);
	useEffect(() => {
		if (!props.team.users) return;
		let a = [] as ProfileData[];
		let m = [] as ProfileData[];
		for (let u of props.team.users) {
			if (u.level) a.push(u);
			else m.push(u);
		}
		setAdmins(a);
		setMembers(m);
	}, [setAdmins, setMembers, props.team])

	const addUser = useCallback((u: ProfileData) => POST({
		url: ['team', props.team.id, 'addUser'].join('/'),
		data: { username: u.username }
	})
		.then(props.updateProfile), [props.updateProfile, props.team.id]);

	return <>
		<section className='container'>
			<h1><b>Admins</b></h1>
			<div className={styles.userTable}>
				{admins.map((u: ProfileData, i: number) => {
					return <>
						<div key={['status', i].join('.')} className={styles.status}>
							{u.isLicensed ? <FaCrown /> : <FaCircle style={{fontSize: '0.5em'}} />}
						</div>
						<Link key={['username', i].join('.')} className={styles.profile} to={'/@/' + u.username}>
							{u.username}
						</Link>
					</>
				})}
			</div>
		</section>
		<section className='container'>
			<h1><b>Members</b></h1>
			<div className={styles.userTable}>
				{
					props.isAuthed ?
					<>
						<div />
						<div className={styles.searchBar}>
							<FaPlusCircle />
							<AutoComplete
								onChange={() => {}}
								onSubmit={addUser}
								action=''
								options={users}
								convert={(u: ProfileData) => u.username}
								styles={styles}
								placeholder='Add new member'
							/>
						</div>
					</> :
					null
				}
				{members.map((u: ProfileData, i: number) => {
					return <>
						<div key={['status', i].join('.')} className={styles.status}>
							{u.isLicensed ? <FaCrown /> : <FaCircle style={{fontSize: '0.5em'}} />}
						</div>
						<Link key={['username', i].join('.')} className={styles.profile} to={'/team/' + u.username}>
							{u.username}
						</Link>
					</>
				})}
			</div>
		</section>
	</>
}