import React, { useEffect, useState, useCallback, ReactElement, useMemo, useReducer, useContext } from 'react';
import { StaticContext } from 'react-router';
import { Route, Switch, RouteComponentProps, Redirect } from 'react-router-dom';

import { GET } from '../utils/requests';
import { Team as TeamInterface, EnhancedPlayer, ProfileData } from '../interfaces';
import { renderTournament, fetchNames } from './hooks';
import NotFound from '../404';
import Info from './Info';
import Tournaments from './Tournaments';
import { Members } from './Teams';

export default function Team(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
	profile: ProfileData
	type: 'player' | 'team'
}): ReactElement | null {

	const [player, setPlayer] = useState(undefined as TeamInterface & ProfileData | null | undefined);
	const updateProfile = useCallback(() => GET({ url: '/' + props.type + '/' + props.id })
		.then(setPlayer)
		.catch(() => setPlayer(null)), [setPlayer, props.id, props.type]);
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);
	const isAuthed = props.id.toLowerCase() === props.profile.uniqueID;

	useEffect(() => {
		if (!player) return;
		if (!player.teamId) return;
		window.history.pushState('', document.title, window.location.pathname.replace(props.id, player.teamId) + window.location.search);
	}, [player, props.id]);

	if (player === undefined) return null;
	if (player === null) return <NotFound />;
	
	return <>
		<div id='main' className={'animated-' + props.location.state?.direction}>
			<Info
				{...props}
				user={props.profile}
				profile={player}
				updateProfile={updateProfile}
				isAuthed={!!player.permissions}
				type='team'
			/>
			<Route render={({location}) => {
				return (
					<Switch location={location}>
						<Route exact path={`/team/${props.id}`} render={() => {
							return <Tournaments {...props} user={props.profile} profile={player} isAuthed={isAuthed} direction={props.location.state.direction.slice(6)}/>	
						}}/>
						<Route exact path={`/team/${props.id}/tournaments`} render={() => {
							return <Tournaments {...props} user={props.profile} profile={player} isAuthed={isAuthed} direction={props.location.state.direction.slice(6)}/>	
						}}/>
						<Route exact path={`/team/${props.id}/members`} render={() => {
							return <Members {...props} team={player} isAuthed={!!player.permissions} updateProfile={updateProfile} />	
						}}/>
					</Switch>
				)
			}} />
		</div>
	</>
}