import React, { useState, useCallback, useContext, useMemo, useEffect } from 'react';
import { StaticContext } from 'react-router';
import { Link, Route, Redirect, RouteComponentProps } from 'react-router-dom';

import styles from './css/user.module.css';
import contactFields, { ContactMeta } from '../resources/contact';
import { UserSwitcher } from '../components/Switcher';
import { ProfileData, Team, ContactEntry } from '../interfaces';
import { FaAngleRight, FaCheckCircle, FaChess, FaUserCircle, FaCogs, FaUsers, FaBan } from 'react-icons/fa';
import { isDev, POST } from '../utils/requests';
import { flags } from '../config.json';
import countries from '../assets/countries.json';
import { useClub } from './callbacks/ChessCom';
import { ChessTeam, TeamData } from 'chess-web-api';
import { capitalise } from '../utils/prototype';
import { LoginContext } from '../App';
import { AlertContext } from '../Contexts';

export default function Info(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
	user: ProfileData
	profile: Team & ProfileData
	updateProfile: () => void
	endSession?: () => Promise<void>
	isAuthed: boolean
	type?: 'user' | 'team' | 'player'
}) {

	const notes = useMemo(() => {
		return Object.entries(props.profile?.meta || {}).map(([k, v]) => {
			if (!v) return null;
			k = k.trim();
			if (k.endsWith('?')) k = k.slice(0, -1);
			if (typeof v === 'boolean') return k;
			k += ': ';
			return k + v.toString();
		}).filter(v => v);
	}, [props.profile]);
	const Notes = useMemo(() => {
		return <div className={styles.notes}>
			{notes.map((line, i) => line ? <div key={['line', i].join('.')}>{line}</div> : null)}
		</div>
	}, [notes]);

	const [shouldFetch, fetchClub] = useState('');
	const chessComClub = useClub(shouldFetch) as TeamData;
	const getName = useCallback((meta: ContactMeta, entry: ContactEntry) => {
		if (entry.name) return entry.name;
		if (entry.username) return entry.username;
		if (entry.id) return entry.id;
		if (meta.id !== 'chessCom') return capitalise(meta.id as string);
		if (chessComClub.name) return chessComClub.name;
		if (entry.link && !shouldFetch) fetchClub(entry.link);
		return capitalise(meta.id);
	}, [fetchClub, chessComClub.name, shouldFetch]);

	const isLoggedIn = useContext(LoginContext);
	const setAlert = useContext(AlertContext);

	const fields = useMemo(() => {
		return Object.entries(contactFields).reverse().map(([k, meta], i) => {
			meta = Object.assign(contactFields[k], { id: k }) as ContactMeta;
			if (!meta) return null;
			let entry =  props.profile[k as keyof typeof props.profile] as ContactEntry | undefined;
			if (k === 'email') entry = props.profile.email ? Object.assign(props.profile.emailMeta || {}, {id: props.profile.email}) as ContactEntry : undefined;
			if (!entry || typeof entry !== 'object' || (props.type === 'user' && !entry.verified)) return null;
			return [meta, entry];
		}).filter(v => v) as [ContactMeta, ContactEntry][];
	}, [props])

	const createTrigger = useCallback(() => {
		setAlert({ title: '', message: '' });
		POST({ url: ['team', props.id, 'create'].join('/') })
			.then(props.updateProfile)
	}, [props.id, props.updateProfile, setAlert])
	const handleCreate = useCallback(async () => {
		let someAuth = false;
		setAlert({
			title: 'Claim team',
			message: <>
				Team <b>{props.profile.name}</b> has the following available authentication methods:
				<div className={styles.auth}>
					{fields.map(([meta, entry], i) => {
						let userF = props.user[meta.id === 'email' ? 'emailMeta' : meta.id as keyof ProfileData] as ContactEntry;
						let name = getName(meta, entry);
						let canAuth: boolean | null | undefined = (() => {
							if (!userF) return null;
							if (entry.id && entry.id === userF.id) return true;
							if (entry.username && entry.username === userF.username) return true;
							if (meta.id === 'chessCom' && chessComClub && chessComClub.admin) {
								if (chessComClub.admin.some(a => a.split('/').pop() === userF.username?.toLowerCase())) return true;
							}
							return false;
						})();
						if (canAuth !== false) someAuth = true;
						if (canAuth === null) return <>
							<Link
								to={'/me/accounts'}
								id={meta.id}
								className={['button', 'link', styles.account].join(' ')}
								onClick={() => setAlert({ title: '', message: '' })}
							>
								{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
								Verify {meta.name || capitalise(meta.id as string)} account
								<FaAngleRight />
							</Link>
							<div />
						</>
						return <>
							<div
								id={meta.id}
								className={['button', 'link', styles.account, canAuth === false ? styles.null : ''].join(' ')}
								onClick={canAuth ? createTrigger : () => {}}
							>
								{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
								{canAuth ?
									<>Claim via {meta.name || capitalise(meta.id as string)}</>
									: name
								}
								{!canAuth ? <FaBan /> : <FaAngleRight />}
							</div>
							<div className={styles.authMessage}>
								{canAuth === false ? <small>{meta.authMessage}</small> : null}
							</div>
						</>
					})}
				</div>
				{!someAuth ?
					Object.values(fields).length ?
						<>You do not have any available authentication methods for this team.<br />Do you definitely own it?</> :
						<>There are no available authentication methods for this team.<br />Submit a request to your Tournament director.</> :
					null
				}
			</>
		})
	}, [createTrigger, fields, props.user, props.profile.name, getName, setAlert, chessComClub]);

	const [redirect, setRedirect] = useState(false);
	if (props.location.search.startsWith('?user=')) window.close();
	if (redirect) return <Redirect to='/' />;

	return (
		<div className={styles.container}>
			<div className={styles.title}>
				<div className={['container', styles.info].join(' ')}>
					<h1>
						<span className='bold'>
							{props.profile.username || props.profile.name}
							{props.profile.teamId ? <FaCheckCircle className='verified' /> : null}
						</span>
					</h1>						
					<div className={styles.details}>
						{isLoggedIn && !props.isAuthed && props.type === 'team' && !props.profile.teamId ?
							<div className={['button', 'link', styles.account, styles.claim].join(' ')} onClick={handleCreate}>
								This team is unclaimed. Claim team?
								<FaAngleRight />
							</div> :
							null
						}
						{props.profile.nationality ? <div className={styles.flag}>
							<img src={flags.replace('|', props.profile.nationality)} alt={props.profile.nationality}/>
							{countries[props.profile.nationality as keyof typeof countries]}
						</div> : null}
						{props.type === 'team' && props.profile.founder ?
							<div>Founder: <Link to={'/@/' + props.profile.founder} className='playerName'>{props.profile.founder}</Link></div> :
							null
						}
						{props.type === 'team' && props.profile.users ?
							<div>Members: <Link to={['', 'team', props.id, 'members'].join('/')} className='playerName'>{props.profile.users.length}</Link></div> :
							null
						}
						<div>Account creation: {new Date(props.profile.createdAt).toString().slice(0, 15)}</div>{/*}
						<div>Tournaments: {tournaments.length}</div>
						<div>Public tournaments: {tournaments.filter(t => t.setting && t.setting.public).length}</div>*/}
					</div>
					<div className={styles.buttons}>
						{props.isAuthed ?
							<div className={styles.row}>								
								<a href={isDev ? 'https://localhost:3000' : 'https://beta.scorchapp.co.uk'} rel='noopener noreferrer' target='_blank' className={['button', 'link', styles.new].join(' ')}>
									Create New Tournament
									<FaAngleRight />
								</a>							
								{props.endSession ? <div className={['button', 'link', styles.logout].join(' ')} onClick={() => {
									if (props.endSession) props.endSession().then(() => setRedirect(true));
								}}>
									Log out
									<FaAngleRight />
								</div> : null}
							</div> :
						null}					
						{!isLoggedIn ?
							<div className={styles.row}>Sign in to view contact details</div> :
						!Object.values(fields).length ?
							<div className={styles.row}>No contact details registered</div> :
						fields.map(([meta, entry], i) => {
							return (
								<div className={styles.row} key={['row', i].join('.')}>
									{entry.link || meta.idLink || meta.link ?
										<a
											id={meta.id}
											href={entry.link || (meta.idLink || meta.link as string).replace('|', entry?.username || '')}
											className={['button', 'link', styles.account].join(' ')}
											rel='noopener noreferrer'
											target='_blank'
										>
											{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
											{getName(meta, entry)}
											{entry.verified ? <FaCheckCircle className='verified' /> : null}
										</a> :
										<div
											id={meta.id}
											className={['button', 'link', styles.account].join(' ')}
										>
											{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
											{getName(meta, entry)}
											{entry.verified ? <FaCheckCircle className='verified' /> : null}
										</div>
									}
								</div>
							);
						})}
						<div className={styles.row}>
							{notes.length && notes.some(n => n) ? Notes : null}
						</div>
					</div>
					<Route path={`/team/${props.id}/:section?`} render={(rProps) => {
						return <UserSwitcher
							section={rProps.match.params.section}
							loaded
							sections={{
								tournaments: {
									link: `/team/${props.id}/tournaments`,
									icon: <FaChess />
								},
								members: {
									hide: !props.profile.players && !props.profile.teamId,
									link: `/team/${props.id}/members`,
									icon: <FaUsers />
								},
								accounts: {
									hide: !props.isAuthed,
									link: `/team/${props.id}/accounts`,
									icon: <FaUserCircle />
								},
								settings: {
									hide: !props.isAuthed,
									link: `/team/${props.id}/settings`,
									icon: <FaCogs />
								}
							}}
						/>
					}} />
					<Route exact path={`/@/${props.profile.username}/:section?`} render={(rProps) => {
						return <UserSwitcher
							section={rProps.match.params.section}
							loaded
							sections={{
								tournaments: {
									link: `/@/${props.profile.username}/tournaments`,
									icon: <FaChess />
								},
								teams: {
									link: `/@/${props.profile.username}/teams`,
									icon: <FaUsers />
								},
								accounts: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/accounts`,
									icon: <FaUserCircle />
								},
								settings: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/settings`,
									icon: <FaCogs />
								}
							}}
						/>
					}} />
					
				</div>
			</div>
		</div>
	)
}