import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { Redirect, RouteComponentProps, Route, Switch } from 'react-router-dom';
import { StaticContext } from 'react-router';
import Tournaments from './Tournaments';
import { ProfileData, Team } from '../interfaces';
import Info from './Info';
import { SettingsComponent, Accounts, Teams } from '.'
import { GET, POST } from '../utils/requests';
import * as regexes from '../utils/regexes';
import NotFound from '../404';
import Verify from './Verify';

export default function User(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
	profile: ProfileData
	endSession: () => Promise<void>
}) {

	const [profile, setProfile] = useState(undefined as Team & ProfileData | null | undefined);
	const id = useMemo(() => profile?.username || props.id, [props.id, profile]);

	const updateProfile = useCallback(async () => {
		if (!regexes.username.test(props.id)) return setProfile(null);
		GET({ url: '/user/' + props.id })
			.then(setProfile)
			.catch(() => setProfile(null));
	}, [props.id, setProfile])
	useEffect(() => {
		updateProfile();
	}, [updateProfile]);

	const isAuthed = props.id.toLowerCase() === props.profile.uniqueID;

	if (profile === undefined) return null;
	if (!profile) return <NotFound />;

	return (<>
        <div id='main' className={'animated-' + props.location.state?.direction}>
			<Info
				{...props}
				user={props.profile}
				profile={profile}
				updateProfile={updateProfile}
				isAuthed={isAuthed}
				type='user'
			/>
			<Route render={({location}) => {
				return (
					<Switch location={location}>
						<Route exact path={`/@/${id}`} render={() => {
							return <Tournaments {...props} user={props.profile} profile={profile} isAuthed={isAuthed} direction={props.location.state.direction.slice(6)}/>	
						}}/>
						<Route exact path={`/@/${id}/verify/:account?`} render={(rProps) => {
							if (!isAuthed) return null;
							return <Verify {...props} id={id} updateProfile={updateProfile}/>;
						}}/>
						<Route exact path={`/@/${id}/tournaments`} render={() => {
							return <Tournaments {...props} user={props.profile} profile={profile} isAuthed={isAuthed} direction={props.location.state.direction.slice(6)}/>	
						}}/>
						<Route exact path={`/@/${id}/teams`} render={() => {
							if (!isAuthed) return null;
							return <Teams {...props} profile={profile} direction={props.location.state.direction.slice(6)} updateProfile={updateProfile}/>	
						}}/>
						<Route exact path={`/@/${id}/accounts`} render={() => {
							if (!isAuthed) return null;
							return <Accounts {...props} profile={profile} direction={props.location.state.direction.slice(6)} updateProfile={updateProfile}/>	
						}}/>
						<Route exact path={`/@/${id}/settings`} render={() => {
							if (!isAuthed) return null;
							return <SettingsComponent {...props} profile={profile} direction={props.location.state.direction.slice(6)} updateProfile={updateProfile}/>	
						}}/>
					</Switch>
				)
			}} />		
		</div>
	</>)
}