import React, { useState, useCallback, useEffect, useMemo, useReducer } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import { GET } from '../utils/requests';
import { TournamentStatus, TournamentSettings, ProfileData, Team, EnhancedPlayer } from '../interfaces';
import SectionBreak from '../components/SectionBreak';
import styles from './css/tournaments.module.css';
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa';
import { renderTournament, fetchNames } from './hooks';

export default function Tournaments(props: RouteComponentProps<any, StaticContext, any> & {
	direction: string
	user: ProfileData
	profile: ProfileData & Team
	isAuthed: boolean
}) {

	const [managed, setManaged] = useState([] as (TournamentStatus & { setting: TournamentSettings })[]);
	const updateManaged = useCallback(() => GET({ url: '/profile/tournaments' })
		.then(setManaged), [setManaged]);
	useEffect(() => {
		if (!props.isAuthed) return;
		updateManaged();
	}, [updateManaged, props.isAuthed]);

	const [active, inactive] = useMemo(() => {
		let arr = [[], []] as [EnhancedPlayer[], EnhancedPlayer[]];
		if (!props.profile) return arr;
		for (let p of props.profile.tournaments) {
			if (p.active && p.tournament.active) arr[0].push(p);
			else arr[1].push(p);
		}
		return arr;
	}, [props.profile]);
	const [playerNames, setPlayerNames] = useState({});
	const [hasFetched, setFetch] = useReducer((state: number) => state += 1, 0);
	const activeTournaments = useMemo(() => active.map((v) => {
		let [elem, shouldFetch] = renderTournament(v,playerNames);
		if (shouldFetch.length && hasFetched < 5) fetchNames(shouldFetch).then(setPlayerNames).then(setFetch);
		return elem;
		/* eslint-disable-next-line react-hooks/exhaustive-deps */
	}), [active, playerNames, setPlayerNames, setFetch]);
	const inactiveTournaments = useMemo(() => inactive.map((v) => {
		let [elem, shouldFetch] = renderTournament(v, playerNames);
		if (shouldFetch.length && hasFetched < 5) fetchNames(shouldFetch).then(setPlayerNames).then(setFetch);
		return elem;
		/* eslint-disable-next-line react-hooks/exhaustive-deps */
	}), [inactive, playerNames, setPlayerNames, setFetch]);
	
	return (<>
		{!managed.length ? null : <section className={'animated-' + props.direction} key='standings.1'>
			<SectionBreak name='MANAGED TOURNAMENTS'/>
			<div className={['container'].join(' ')}>
				<table className={[styles.tableAnnounce].join(' ')}>
					<thead>
						<tr>
							<th style={{width: '20px' }}>Manage</th>
							<th>Name</th>
							<th>Started</th>
							<th>Round</th>
							<th>View</th>
						</tr>
					</thead>
					<tbody>
						{managed.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf()).map((tourn, i) => {
							let dateStarted = new Date(tourn.createdAt).toString();
							return (
								<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
									<td><a className={styles.tournamentLink} href='https://beta.scorchapp.co.uk'>
										<FaAngleLeft />
									</a></td>
									<td>
										{tourn.name || tourn.id}
									</td>
									<td>
										{dateStarted.slice(0, 11)}
									</td>
									<td>
										{tourn.round}
									</td>
									<td>
										{tourn.setting && tourn.setting.public ?
										<Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
											<FaAngleRight />
										</Link>
										: null}
									</td>
								</tr>
							);
						})}				
					</tbody>
				</table>
			</div>
		</section>}
		<SectionBreak name='ACTIVE TOURNAMENTS' />
		{!active.length ? null : activeTournaments}
		<SectionBreak name='INACTIVE TOURNAMENTS' />
		{!inactive.length ? null : inactiveTournaments}
		<section />
	</>)

}